.loader {
    display: flex;
    justify-content: center;
    text-align: center;
}

.circle {
    border-radius: 50%;
    animation: animate 2s infinite linear;
}

.circle:nth-child(1) {
    animation-delay: 0.5s;
}
.circle:nth-child(2) {
    animation-delay: 1s;
}
.circle:nth-child(1) {
    animation-delay: 1.5s;
}

.small {
    padding: 0.5rem 0;
}
.circle-small {
    background-color: #fff;
    width: 0.5rem;
    height: 0.5rem;
    margin: 6px 2px;
}
.circle-small:nth-child(1) {
    margin-left: 0.6rem;
}

.medium {
    width: 100%;
    margin: 20vh auto;
}

.circle-medium {
    background-color: var(--blue-solid);
    width: 3rem;
    height: 3rem;
    margin: 0 4px;
}
.large {
    width: 100%;
    margin: calc(50vh - 125px) auto;
}

.circle-large {
    background-color: var(--blue-solid);
    width: 3rem;
    height: 3rem;
    margin: 0 4px;
}

@keyframes animate {
    0%,
    100% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }
}
