.create {
    overflow: hidden;
    padding-bottom: 12rem;
    min-height: calc(100vh - 250px);
}

.create-content {
    display: flex;
    justify-content: center;
}

.section-head {
    text-align: center;
    margin-bottom: 10rem;
}

.heading {
    padding-top: 2rem;
    display: block;
    font-size: 4rem;
    text-transform: uppercase;
}

.sub-heading {
    font-size: 1.7rem;
    font-family: 'Courgette', cursive;
    font-weight: 300;
    color: #a5a5a5;
}

.boat-wrap {
    display: none;
}
.container-form {
    border: 1px solid #f6f6f6;
    background-color: #fff;
    padding: 2rem 1.5rem;
    border-radius: 10px;
    margin: 1.5rem;
    width: 100%;
    max-width: 30rem;
    position: relative;
    border: 0px solid var(--aqua-solid);
    box-shadow: rgb(204, 219, 232) 3px 3px 6px 0px inset,
        rgba(255, 255, 255, 0.5) -3px -3px 6px 1px inset;
}
.container-form::before,
.container-form::after {
    content: '';
    position: absolute;
    border-radius: 15px;
    z-index: -1;
}
.container-form::before {
    width: 90%;
    height: 120%;
    background: var(--gradient-color);
    top: -10%;
    left: 20%;
}

.container-form::after {
    background: var(--gradient-color-alt);
    width: 8rem;
    height: 8rem;
    left: -5%;
    bottom: -5%;
    z-index: -2;
}

.container-form .title-form {
    font-weight: 600;
    margin-bottom: 1.8rem;
}

.create-form .field {
    margin: 1rem 0 1rem 0;
}

.create-form label,
.create-form input {
    width: 100%;
}

.create-form label {
    font-size: 2rem;
}
.create-form input {
    font-family: 'OpenSans', sans-serif;
    font-size: 2rem;
    padding: 0.5rem;
    border: 1px solid #9d9dc4;
    border-radius: 5px;
    outline: none;
    transition: all 0.3s ease-in;
}
.create-form input:focus {
    box-shadow: 0 3px 7px rgba(0, 0, 0, 0.1);
}

.file-btn::-webkit-file-upload-button {
    display: none;    
}

.file-btn {
    letter-spacing: 1.5px;
}
.textarea {    
    width: 100%;
    padding-top: 0.8rem;
    letter-spacing: 2px;
    resize: none;
    font-family: 'OpenSans', sans-serif;
    font-size: 2rem;
    padding: 0.5rem;
    border: 1px solid #9d9dc4;
    border-radius: 5px; 
    outline: none;   
}

.options{
    width: 100%;
    font-family: 'OpenSans', sans-serif;
    font-size: 2rem;
    padding: 0.5rem;
    border: 1px solid #9d9dc4;
    border-radius: 5px;
    outline: none;
}

::-webkit-input-placeholder {
   font-size: 1.65rem;
}
::-moz-placeholder {
    font-size: 1.65rem;
}

.error {
    display: flex;
    color: #ff525b;
    font-size: 1.5rem;
    border-radius: 1rem;
}
@media all and (min-width: 420px) {
    .container-form {
        max-width: 40rem;
    }
}

@media all and (min-width: 768px) {
    .create-content {
        justify-content: space-between;
    }
    .boat-wrap {
        display: block;
        flex: 0 0 45%;
    }
    .boat-wrap img {
        width: 100%;
    }
    .create-form {
        flex: 0 0 50%;
        align-self: flex-start;
    }

    .input-group-wrap {
        display: flex;
    }
    .input-group-wrap .field {
        width: 50%;
        margin-right: 2rem;
    }
    .input-group-wrap .field:last-child {
        margin-right: 0;
    }
    .create-form .field {
        margin: 1rem 1.5rem 1rem 0;
    }
    .container-form {
        max-width: 90%;
    }

    .error {
        display: flex;
        /* position: absolute;
        right: 5%; */
        color: #ff525b;
        font-size: 1.5rem;
        border-radius: 1rem;
    }
}